import m from 'mithril'
import { routes } from "../routes"
import '../Material/material.scss'
import Sidebar from '../components/sidebar/sidebar'

const Users = {
    oninit(vnode){
        routes.settile()
    },
    view(vnode){
        return <>
        <Sidebar />
        <main>
            Users
        </main>
    </>
    }
}
export default Users